input {
    display: flex;
    background-color: #ffffff2a;
    border: 1px solid #FFF;
    color: #FFF;
    border-radius: 10px;
    width: 70%;
    max-width: 70%;
    padding: 20px 15px;
    font-size: 16px;
}

input:focus-visible {
    outline: none;
}

input::placeholder {
    color:#b8b8b8
}

input:disabled {
    opacity: 60%;
}